@import url(./reset.css);

div.nav{
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    ul{
        &::after{
            display: block;
            content: "";
            clear: both;
        }
        a{
            float: left;
            width: 25%;
            color: #000;
            text-decoration: none;
            li{
                position: relative;
                height: 55px;
                font-size: 20px;
                font-weight: bold;
                text-align: center;
                background: #accbee;
                color: #fff;
                cursor: pointer;
                box-sizing: border-box;
                transition: all ease 0.5s 0s;
                span{
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translateX(-50%) translateY(-50%);
                }
                &:hover{
                    background: #e7f0fd;
                    color: #000;
                }
                
            }
        }
        
    }
}